.chat-container {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  padding: 1rem;
  background-color: #e5ddd5;
  border-radius: 8px;
  max-height: 80vh;
  width: 100%;
  overflow-y: auto;
  font-family: 'Segoe UI', sans-serif;
  overflow-x: hidden;
  box-sizing: border-box;
}

/* Contact Details */
.contact-details {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  padding: 15px;
  background-color: #dfdfdf;
  border: 1px solid #ccc;
  border-radius: 8px;
  width: 100%;
  box-sizing: border-box;
}

.avatar {
  width: 40px;
  height: 40px;
  background-color: #888;
  color: white;
  font-weight: bold;
  font-size: 1.2rem;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Chat Messages */
.chat-message {
  display: flex;
  align-items: flex-start;
  max-width: 75%;
  word-wrap: break-word;
}

.chat-message.user {
  align-self: flex-end;
  flex-direction: row-reverse;
  text-align: right;
}

.chat-message.other {
  align-self: flex-start;
  text-align: left;
}

.chat-bubble {
  background-color: #ffffff;
  padding: 0.75rem 1rem;
  border-radius: 16px;
  position: relative;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
  word-wrap: break-word;
  max-width: 100%;
}

.chat-message.user .chat-bubble {
  background-color: #dcf8c6;
  border-bottom-right-radius: 0;
}

.chat-message.other .chat-bubble {
  background-color: #ffffff;
  border-bottom-left-radius: 0;
}

.chat-header {
  font-size: 0.75rem;
  color: #555;
  margin-bottom: 0.25rem;
  display: flex;
  justify-content: space-between;
}

.sender {
  font-weight: bold;
}

.timestamp {
  font-style: italic;
  font-size: 0.7rem;
  color: #888;
}

.chat-text {
  font-size: 0.95rem;
  line-height: 1.4;
}

/* Responsive Design */
@media (max-width: 768px) {
  .chat-container {
    padding: 0.5rem;
    gap: 0.75rem;
  }

  .chat-message {
    max-width: 90%;
  }

  .chat-bubble {
    padding: 0.6rem 0.9rem;
  }

  .contact-details {
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: 10px;
  }

  .avatar {
    width: 36px;
    height: 36px;
    font-size: 1rem;
  }
}
